import { useState } from "react";
import { useLocation } from "react-router-dom";
import useFormValidation from "../hooks/useFormValidation";
import { TextRegularBold, TextSmall } from "../assets/styles/Text";
import { FlexWrapper } from "../assets/styles/FlexWrapper";
import FormInput from "./FormInput";
import PopupContactsTypeSelect from "./PopupContactsTypeSelect";
import RoleSelectPopup from "./RoleSelectPopup";
import CourseSelectPopup from "./CourseSelectPopup";
import { ButtonPrimary, ButtonClosePopup } from "../assets/styles/Button";
import { InputCheckbox } from "../assets/styles/Checkbox";
import { LabelRadio } from "../assets/styles/Radio";
import { useEffect } from "react";

function PopupWithForm({ handleClose }) {
  const { inputsValue, isValid, getInputValue, onChange, clearValidation } = useFormValidation();
  const [currentVacancy, setCurrentVacancy] = useState({});
  const location = useLocation();

  function handleSubmit(evt) {
    evt.preventDefault();

    console.log(inputsValue);

    handleClose();
  }

  useEffect(() => {
    if (location.pathname === "/vacancy") {
      const vacancy = JSON.parse(localStorage.getItem('vacancy'));
      setCurrentVacancy(vacancy);

      getInputValue("vacancy", vacancy.name);
    }

    return (() => {
      clearValidation();
    })
  }, []);

  return (
    <FlexWrapper
      as="form"
      direction="column"
      padding="40px 90px"
      color="white"
      radius="60px"
      maxWidth="600px"
      gap="25px"
      position="relative"
      onChange={onChange}
      onSubmit={handleSubmit}
    >
      <TextRegularBold
        align="center"
      >
        {location.pathname === "/" && "        "}
        {location.pathname === "/vacancy" &&
          (currentVacancy.role === "mentor" ? `     ${currentVacancy.name}`
          : `     ${currentVacancy.name}`)
        }
      </TextRegularBold>
      <FlexWrapper
        direction="column"
      >
        <FormInput
          type="text"
          name="name"
          placeholder=""
          minLength="2"
          maxLength="30"
          getInputValue={getInputValue}
        />
        <FormInput
          type="email"
          name="email"
          placeholder="Email"
          getInputValue={getInputValue}
        />
        <PopupContactsTypeSelect
          getInputValue={getInputValue}
        />
        {location.pathname === "/vacancy" &&
          <>
            <FormInput
              type="url"
              name="url"
              placeholder="  "
              getInputValue={getInputValue}
            />
            <FlexWrapper
              direction="row"
              gap="10px"
              maxWidth="400px"
            >
              <InputCheckbox
                name="contact-checkbox"
                id="contact-checkbox"
                value="agree"
                required
              />
              <LabelRadio
                id="contact-checkbox"
              >
                     
              </LabelRadio>
            </FlexWrapper>
          </>
        }
      </FlexWrapper>
      {location.pathname === "/" &&
        <>
          <RoleSelectPopup
            getInputValue={getInputValue}
          />
          <CourseSelectPopup
            getInputValue={getInputValue}
          />
          <FlexWrapper
            direction="column"
          >
            <FormInput
              type="url"
              name="url"
              placeholder="  "
              getInputValue={getInputValue}
            />
            <FlexWrapper
              direction="row"
              gap="10px"
              maxWidth="400px"
            >
              <InputCheckbox
                name="contact-checkbox"
                id="contact-checkbox"
                value="agree"
                required
              />
              <LabelRadio
                id="contact-checkbox"
              >
                     
              </LabelRadio>
            </FlexWrapper>
          </FlexWrapper>
        </>
      }
      <FlexWrapper
        margin="0 auto"
      >
        <ButtonPrimary
          type="submit"
          disabled={!isValid}
        >
          <TextSmall white>
            
          </TextSmall>
        </ButtonPrimary>
      </FlexWrapper>
      <ButtonClosePopup
        type="reset"
        onClick={handleClose}
        top="30px"
        right="40px"
      />
    </FlexWrapper>
  );
}

export default PopupWithForm;