import { useEffect } from 'react';
import { FlexWrapper } from '../assets/styles/FlexWrapper';
import { ButtonRadioXS } from '../assets/styles/Radio';
import { TextRadio, TextRadioSmall } from '../assets/styles/Text';
import RadioButton from "./RadioButton";

function CourseSelectPopup({ getInputValue }) {
  useEffect(() => {
    getInputValue('course', 'programming')
  }, []);

  function handleChange(evt) {
    const { name, value } = evt.target;
    getInputValue(name, value);
  }

  return (
    <FlexWrapper
      direction="column"
      gap="10px"
    >
      <TextRadio>
         
      </TextRadio>
      <FlexWrapper
        as="fieldset"
        direction="row"
        gap="10px 4px"
        wrap="wrap"
      >
        <RadioButton
          Button={ButtonRadioXS}
          TextElement={TextRadioSmall}
          name="course"
          id="course-programming-popup"
          value="programming"
          text=""
          defaultChecked
          handleChange={handleChange}
        />
        <RadioButton
          Button={ButtonRadioXS}
          TextElement={TextRadioSmall}
          name="course"
          id="course-analytics-popup"
          value="analytics"
          text=""
          handleChange={handleChange}
        />
        <RadioButton
          Button={ButtonRadioXS}
          TextElement={TextRadioSmall}
          name="course"
          id="course-design-popup"
          value="design"
          text=""
          handleChange={handleChange}
        />
        <RadioButton
          Button={ButtonRadioXS}
          TextElement={TextRadioSmall}
          name="course"
          id="course-marketing-popup"
          value="marketing"
          text=""
          handleChange={handleChange}
        />
        <RadioButton
          Button={ButtonRadioXS}
          TextElement={TextRadioSmall}
          name="course"
          id="course-management-popup"
          value="management"
          text=""
          handleChange={handleChange}
        />
      </FlexWrapper>
    </FlexWrapper>
  );
}

export default CourseSelectPopup;